	module.exports = {"data":{"markdownRemark":{"frontmatter":{"date":"March 13, 2018","time":{"start":"6:00pm","end":"7:00pm"},"sponsor":{"frontmatter":{"name":"Robert Half","site":"https://www.roberthalf.com/ia-des-moines"}},"host":{"frontmatter":{"location":"Gravitate","address":"317 6th Ave, Suite 10"}},"talk":{"html":"<p>\"Transducer\" sounds like just another jargony functional programming term and,\nyeah, it kind of is...</p>\n<p>Consider this example (a classic intro to FP in JS example):</p>\n<div class=\"gatsby-highlight\" data-language=\"js\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token number\">2</span><span class=\"token punctuation\">,</span> <span class=\"token number\">3</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">,</span> <span class=\"token number\">5</span><span class=\"token punctuation\">]</span>\n  <span class=\"token punctuation\">.</span><span class=\"token function\">map</span><span class=\"token punctuation\">(</span>x <span class=\"token operator\">=></span> x <span class=\"token operator\">*</span> x<span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">.</span><span class=\"token function\">filter</span><span class=\"token punctuation\">(</span>x <span class=\"token operator\">=></span> x <span class=\"token operator\">%</span> <span class=\"token number\">2</span><span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<p>Performance-minded people might point out that we are looping through the array 2\ntimes. And while that isn't going to cause any problems with an array of this size\nand with these simple operations, it can become an issue when the scale is different.</p>\n<p>Here's where transducers come in. (More FP jargon coming...) Transducers let us\ncompose our transformation functions (the map and filter) into a single reducer,\nwhich we can use to perform the same functionality on the array, but we'll only\nloop over it 1 time.</p>\n<p>This talk will go over the basics of map, filter, reduce and function composition.\nThen build up gradually to transducers.</p>","frontmatter":{"title":"An Introduction to Transducers","speaker":{"frontmatter":{"name":"Eric Ponto"}}}}}}},"pathContext":{"slug":"/2018-03-13/"}}

